.Form-wrapper {
  background-color: rgba(0, 0, 0, 0);
  transition: 400ms top;
  z-index: 2;
  display: flex;
  justify-content: center; }
  .Form-wrapper .Form {
    z-index: 3;
    user-select: none;
    background-color: #f4f4f2;
    padding: 20px 30px 30px;
    min-width: 250px;
    min-height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    position: absolute;
    transition: 600ms top;
    top: -250px; }
    .Form-wrapper .Form .title {
      width: 100%;
      font-size: 1.5rem;
      text-align: center; }
      .Form-wrapper .Form .title::after {
        content: "";
        display: block;
        width: 80%;
        background-color: #d5d1ca;
        height: 1px;
        margin: 0 auto 7px; }
    .Form-wrapper .Form .input-wrapper {
      margin: 7px 0 0; }
      .Form-wrapper .Form .input-wrapper .error {
        text-align: center; }
        .Form-wrapper .Form .input-wrapper .error .Button {
          margin: 12px auto 0;
          display: block; }
      .Form-wrapper .Form .input-wrapper .button-wrapper {
        margin-top: 12px; }
      .Form-wrapper .Form .input-wrapper .input {
        cursor: pointer;
        border: 1px solid #d5d1ca;
        border-radius: 15px;
        width: 100%;
        padding: 5px 23px;
        font-size: 1.2rem;
        margin: 5px auto;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 300ms opacity;
        position: relative; }
        .Form-wrapper .Form .input-wrapper .input:hover {
          opacity: .8;
          border-color: #b1ada8; }
        .Form-wrapper .Form .input-wrapper .input .Color-Dot {
          margin: 0 7px 0 0; }
    .Form-wrapper .Form .Button {
      margin: 3px; }
  .Form-wrapper.active .Form {
    top: calc(50% - 125px); }
  .Form-wrapper .Form-Shade {
    width: 100vw;
    height: 100vh;
    z-index: 2;
    animation: fadein 400ms;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.7); }

.rc-trigger-popup {
  z-index: 4; }
  .rc-trigger-popup .rc-color-picker-panel-inner .rc-color-picker-panel-wrap:nth-child(3) {
    display: none; }
    @media (min-width: 992px) {
      .rc-trigger-popup .rc-color-picker-panel-inner .rc-color-picker-panel-wrap:nth-child(3) {
        display: block; } }

@-webkit-keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-moz-keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-o-keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadein {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
